  font-weight: 600;
  font-style: normal;
}
.rendered-content {
  display: none;
}
body {
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  font-family: "FFMark", Avenir, Helvetica, sans-serif;
  font-weight: 300;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity 1s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
@font-face {
  font-family: 'FFMark';
  src: url("~styles/fonts/ffmark/medium.eot");
  src: url("~styles/fonts/ffmark/medium.eot?#iefix") format("embedded-opentype"), url("~styles/fonts/ffmark/medium.woff") format("woff"), url("~styles/fonts/ffmark/medium.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
