#wpadminbar {
  background: #000;
  padding-left: 30px;
  transition: all 100ms ease-in-out;
  transition-delay: 200ms;
  top: -32px !important;
  z-index: 1000;
  position: fixed;
}
#wpadminbar:after {
    color: white;
    font-family: dashicons;
    font-size: 18px;
    line-height: 32px;
    content: "\f464";
    position: fixed;
    width: 32px;
    height: 32px;
    text-align: center;
    background: black;
    top: 0;
    left: 0;
    z-index: 0;
    cursor: pointer;
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
}
#wpadminbar:hover {
    top: 0 !important;
    transition-delay: 0ms;
}
#wpadminbar #wp-admin-bar-customize,
  #wpadminbar #wp-admin-bar-wp-logo,
  #wpadminbar #wp-admin-bar-comments,
  #wpadminbar #wp-admin-bar-new-post,
  #wpadminbar #wp-admin-bar-new-media {
    display: none;
}



